@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;1,100&display=swap);
:root{--font-primary: #1d1d1d;--navHeight: 55px;font-size:16px;--blue: #126fd3;--indigo: #6610f2;--purple: #6f42c1;--pink: #e83e8c;--red: #dc3545;--orange: #fd7e14;--yellow: #ffc107;--green: #28a745;--teal: #20c997;--cyan: #17a2b8;--white: #fff;--gray: #6c757d;--gray-dark: #343a40;--primary: #007bff;--secondary: #6c757d;--success: #28a745;--info: #17a2b8;--warning: #ffc107;--danger: #dc3545;--light: #f8f9fa;--dark: #343a40;--breakpoint-xs: 0;--breakpoint-sm: 576px;--breakpoint-md: 768px;--breakpoint-lg: 992px;--breakpoint-xl: 1200px;--font-family-roboto: 'Roboto', sans-serif}*{margin:0;padding:0}li,button,a,svg,ul{display:block;-webkit-tap-highlight-color:transparent}body{font-family:'Roboto', sans-serif;font-family:var(--font-family-roboto);color:#1d1d1d;color:var(--font-primary)}.page-center{width:80%;max-width:1200px;margin:0 auto}.page-header{display:flex;justify-content:space-between;align-items:center;width:100%;margin:1rem 0;padding-bottom:1rem;border-bottom:1px solid gray}.border-white{padding:0.4rem;border:1px solid white;border-radius:1px}.border-gray{padding:0.4rem;border:1px solid gray;border-radius:1px}.bg-danger{background:#dc3545 !important;background:var(--danger) !important}.btn{padding:10px 13px;background:#28a745;background:var(--green);border:none;outline:none;border-radius:5px;font-size:1rem;color:white;letter-spacing:1.5px;margin-left:10px;text-decoration:none;text-align:center}.btn:hover{background:darkgreen;transform:scale()}.input-wrapper{display:flex;margin-bottom:1rem;border:1px solid gray}.input-wrapper>input[type='text']{flex:1 1;outline:none;border:none;font-size:1rem;padding:0 10px;min-width:30px;max-width:100%}.input-name{background:black;color:white;padding:10px;display:flex;align-items:center;min-width:50px}@media all and (max-width: 430px){:root{font-size:14px}}@media all and (min-width: 1440px){:root{font-size:16px}}

.Loading{position:absolute;top:50vh;left:50vw;transform:translate(-50%, -50%);font-size:2rem;-webkit-animation:LoadingAnimation 1s linear infinite;animation:LoadingAnimation 1s linear infinite}@-webkit-keyframes LoadingAnimation{0%{transform:none}100%{transform:rotate(360deg)}}@keyframes LoadingAnimation{0%{transform:none}100%{transform:rotate(360deg)}}

.message-alert{position:fixed;z-index:50;bottom:2rem;left:50vw;transform:translateX(-50%);display:flex;align-items:center;padding:10px;border-radius:5px;color:white;font-size:1.2rem;text-align:center}.message-alert.error{background:var(--danger)}.message-alert.success{background:var(--success)}.message-alert--close{background:none;outline:none;border:none;margin-left:10px;font-size:1rem;color:white;cursor:pointer}

.navbar{height:var(--navHeight);position:relative}.navbar.admin{background:var(--blue)}.navbar.blog{background:var(--indigo)}.navbar-center{height:100%;display:flex;justify-content:space-between;align-items:center}.navbar-center.admin{background:var(--blue)}.navbar-center.blog{background:var(--indigo)}.navbar-logo a{color:white;font-size:1.2rem;letter-spacing:3px;text-transform:uppercase;cursor:pointer;text-decoration:none}.navbar-nav{position:fixed;z-index:100;top:0;left:0;height:100vh;width:70vw;max-width:500px;background:#202020;display:flex;flex-direction:column;transform:translateX(-100%);transition:all 0.3s ease}.navbar-nav-title{color:white;min-height:var(--navHeight);border-bottom:1px solid white;letter-spacing:3px;text-transform:uppercase;font-size:1.5rem;display:flex;align-items:center;justify-content:center}.navbar-nav--show{transform:translateX(0)}.navbar-nav--closer{position:fixed;z-index:90;top:0;left:0;width:100vw;max-width:100%;height:100vh;background:rgba(0,0,0,0.3);transition:all 0.3s ease}.navbar-nav .nav-item--last{margin-top:auto}.navbar-nav .nav-link{padding:1rem;text-transform:uppercase;text-decoration:none;letter-spacing:2px;color:white;border-bottom:1px solid #5a5a5a}.navbar-nav .nav-link:hover{background-color:black}.navbar-btn{color:white;font-size:1.2rem;position:relative;z-index:100}@media all and (min-width: 769px){.navbar-center{height:100%;display:flex;justify-content:flex-start;align-items:center;background:var(--blue)}.navbar-logo{margin-right:1rem}.navbar-nav{position:relative;background:none;height:auto;width:auto;max-width:none;flex:1 1;align-items:center;flex-direction:row;transform:none;transition:none}.navbar-nav-title{display:none}.navbar-nav--show{transform:none}.navbar-nav--closer{display:none !important}.navbar-nav .nav-item--last{margin-top:0;margin-left:auto}.navbar-nav .nav-link{padding:10px;text-transform:uppercase;text-decoration:none;letter-spacing:2px;color:white;border-bottom:none}.navbar-nav .nav-link:hover{background-color:black}.navbar-btn{display:none}}


.PostCard{display:grid;width:100%;box-sizing:border-box;grid-template-columns:250px 1fr;grid-template-rows:auto auto 1fr auto;min-height:250px;margin-bottom:1rem;padding:0}.PostCard-image{grid-row:1/-1;border-right:1px solid gray;background-repeat:no-repeat;background-position:center;background-size:cover;width:100%;height:100%}.PostCard-title{padding:1rem;border-bottom:1px solid gray}.PostCard-title a{color:var(--font-primary);text-decoration:none}.PostCard-date{margin:5px 1rem;color:#868686}.PostCard-date--icon{display:inline-block;margin-right:10px}.PostCard-resume{margin:0 1rem}.PostCard-tags{margin:1rem 1rem 0 1rem}.PostCard-tags span{display:inline-block;margin:0 1rem 1rem 0;background:lightgray;padding:5px 10px;border-radius:4px;color:dimgray}.PostCard--mobile{grid-template-columns:none;grid-template-rows:none;text-align:center}.PostCard--mobile .PostCard-image{height:200px;grid-row:auto}.PostCard--mobile .PostCard-title{text-align:center;border-top:1px solid gray}.PostCard--mobile .PostCard-resume{grid-column:auto}

.btn-wrapper{display:flex;margin-left:auto}.react-tagsinput{background-color:#fff;overflow:hidden;padding-left:5px;padding-top:5px;flex:1 1}.react-tagsinput>span{display:flex;flex-wrap:wrap;width:100%}.react-tagsinput--focused{border-color:#a5d24a}.react-tagsinput-tag{background-color:#cde69c;border-radius:2px;border:1px solid #a5d24a;color:#638421;display:flex;font-family:sans-serif;font-size:13px;font-weight:400;margin-bottom:5px;margin-right:5px;padding:5px}.react-tagsinput-remove{cursor:pointer;font-weight:bold;margin-left:5px}.react-tagsinput-tag a::before{content:' ×'}.react-tagsinput-input{background:transparent;border:0;font-family:sans-serif;font-size:13px;font-weight:400;margin-bottom:6px;margin-top:1px;outline:none;padding:5px;flex:1 1}.ce-block__content,.ce-toolbar__content{max-width:none !important}.ce-block__content{border-bottom:1px solid #d4d4d4}.slug-input-btn{display:flex;justify-content:center;align-items:center;background:none;width:30px;outline:none;border:none;font-size:1rem}@media all and (max-width: 550px){.PostEditor-header{flex-direction:column}}

.Blog-grid{display:grid;grid-template-columns:2fr 1fr;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;height:100vh}.Blog-panel-btn,.Blog-panel--closer{display:none}@media all and (max-width: 768px){.Blog-grid{grid-template-columns:none}.Blog-panel.show .Blog-panel-content{transform:none}.Blog-panel.show .Blog-panel--closer{display:block;position:fixed;top:0;left:0;height:100vh;width:100vw;z-index:110;background:rgba(0,0,0,0.11)}.Blog-panel-content{transition:all 0.3s ease;position:fixed;top:0;right:0;bottom:0;width:70vw;max-width:500px;background:var(--primary);z-index:120;transform:translateX(100%)}.Blog-panel-btn{display:block;position:fixed;top:50vh;right:0;transform:translateY(-50%);z-index:100;background:var(--primary);-webkit-text-orientation:upright;text-orientation:upright;-webkit-writing-mode:vertical-lr;-ms-writing-mode:tb-lr;writing-mode:vertical-lr;color:white;padding:5px;border-radius:10px 0 0 10px}}

.blog-header-bg{background:var(--indigo)}.blog-header{padding-top:1rem;border-top:1px solid white;color:white;display:grid;grid-template-columns:1fr 1fr;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;text-transform:capitalize;align-items:center;padding-bottom:1rem}.blog-header-title{font-size:1.2rem}.blog-header-title h1{font-weight:300}.blog-header-author{display:inline-block;background:black;padding:5px 1rem}.blog-header-author h2{font-weight:300;text-align:center;font-size:1.5rem}.blog-header-prof h2{font-weight:100;padding:5px 1rem}@media all and (max-width: 700px){.blog-header{grid-template-columns:none;grid-row-gap:1rem;row-gap:1rem}.blog-header-left{align-items:flex-start}}

.SinglePost-body{display:flex;flex-direction:column;margin-bottom:5rem}.SinglePost-body-p{margin-bottom:1rem;font-size:1.2rem}.SinglePost-body-img{margin-bottom:1rem;text-align:center}.SinglePost-body-img img{width:50%;margin-bottom:0.5rem}.SinglePost-body-img-caption{margin:0 auto;width:70%;letter-spacing:3px}@media all and (max-width: 500px){.SinglePost-body-img img{width:100%}}

